<template>
  <el-table
    ref="table"
    :data="data"
    border
    v-bind="$attrs"
    v-on="$listeners"
    class="multi-level-table"
  >
    <el-table-column v-if="isIndex" type="index" label="序号" width="55" v-bind="indexAttrs" />
    <el-table-column v-if="isSelection" type="selection" width="55" v-bind="selectionAttrs" />
    <template v-for="(item, index) in columns">
      <MultiLevelTableColumn
        :key="item.prop"
        :col="item"
        :colIndex="index"
      >
        <template v-slot:header="{header}">   
          <slot :name="'header-'+header.prop">{{ header.label }}</slot>
        </template>
        <template v-slot:default="{row, col, rowIndex, colIndex, value, prop}">
          <slot
            :row="row"
            :col="col"
            :rowIndex="rowIndex"
            :colIndex="colIndex"
            :value="value"
            :prop="prop"
            :name="prop"
          >
            {{ value }}
          </slot>
        </template>
      </MultiLevelTableColumn>
    </template>
    <el-table-column
      v-if="isOperate"
      label="操作"
      align="center"
      v-bind="operateConfig"
    >
      <template slot-scope="scope">
        <slot name="operate" :row="scope.row" :index="scope.$index"></slot>
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
import MultiLevelTableColumn from './multi-level-table-column.vue'
export default {
  name: 'MultiLevelTable',
  components: {
    MultiLevelTableColumn
  },
  props: {
    data: {
      type: Array,
      default: () => []
    },
    columns: {
      type: Array,
      default: () => []
    },
    isIndex: {
      type: Boolean,
      default: false
    },
    isSelection: {
      type: Boolean,
      default: false
    },
    isOperate: {
      type: Boolean,
      default: false
    },
    indexAttrs: {
      type: Object,
      default: () => {}
    },
    selectionAttrs: {
      type: Object,
      default: () => {}
    },
    operateAttrs: {
      type: Object,
      default: () => {}
    }
  }
}
</script>
