<template>
  <el-table-column
    v-if="!col.children"
    :label="col.label"
    :prop="col.prop"
    show-overflow-tooltip
    v-bind="col"
    align="center"
  >
    <template #header>
      <slot name="header" :header="col">{{ col.label }}</slot>
    </template>
    <template slot-scope="scope">
      <slot name="default" :row="scope.row" :col="col" :rowIndex="scope.$index" :colIndex="colIndex" :value="scope.row[col.prop]" :prop="col.prop" />
    </template>
  </el-table-column>
  <el-table-column
    v-else
    :label="col.label"
    v-bind="col"
    align="center"
  >
    <template #header>
      <slot name="header" :header="col">{{ col.label }}</slot>
    </template>
    <EasyTableColumn
      v-for="(_col, _colIndex) in col.children"
      :key="_col.prop"
      :col-index="_colIndex"
      :col="_col"
    >
      <template v-slot:header="{header}">
        <slot name="header" :header="header">{{ header.label }}</slot>
      </template>
      <template v-slot:default="{row, col, rowIndex, colIndex, value, prop }">
        <slot name="default" :row="row" :col="col" :rowIndex="rowIndex" :colIndex="colIndex" :value="value" :prop="prop" />
      </template>
    </EasyTableColumn>
  </el-table-column>
</template>

<script>
export default {
  name: 'EasyTableColumn',
  props: {
    col: {
      default: () => {},
      type: Object
    },
    colIndex: {
      type: [Number, String],
      default: 0
    }

  }
}
</script>

